body, html {
  height: 100%;
  margin: 0;
}

.container {
  text-align: center;
  height: 100%;
  padding: 16px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr repeat(3, 1fr);
  grid-template-rows: repeat(6, 1fr);
  gap: 15px;
}

.container div {
  padding: 15px;
  font-size: 32px;
  font-family: Helvetica;
  font-weight: bold;
  color: white;
  border-radius: 15px;
}

.header {
  background-color: #FFDE22;
  grid-column: 1 / -1;
  grid-row: 1 / 2;
}

.sidebar {
  background-color: #FF7755;
  grid-column: 1 / 2;
  grid-row: 2 / 4 ;
}

.nav {
  background-color: #00DDFF;
  grid-row: 2/3;
  grid-column: 2 / span 2;
}

.article {
  background-color: #bccbde;
  grid-column: 2/ 4;
}

.article p {
  font-size: 18px;
  font-family: sans-serif;
  color: white;
  text-align: left;
  grid-row: 3 / span 3;
}

.footer {
  background-color: #393f4d;
  grid-column: 1 / 4;
  grid-row: 4/6;
}
